  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  transition: all .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.modal-open-btn:hover {
  background-color: #0a8817;
}

.modal-open-btn:focus {
  fill: #fff;
  color: #fff;
  background-color: #0a8817;
}

.bay-now {
  text-align: center;
  fill: #000;
  color: #000;
  cursor: pointer;
  background-color: #f8d24e;
  border: none;
