.cream, .coffee, .shake {
  letter-spacing: .04em;
  text-transform: uppercase;
  width: 100%;
  height: 136px;
  text-align: center;
  border-bottom-left-radius: 16px;
  border-bottom-right-radius: 16px;
  flex-direction: column;
  justify-content: flex-end;
  padding-bottom: 20px;
  font-size: 16px;
  font-weight: 700;
  line-height: 20px;
  display: flex;
  box-shadow: 0 5px 20px rgba(212, 20, 67, .1);
}

.cream {
  border: 2px solid #96af8c;
}

.coffee {
  border: 2px solid #efd478;
}

.shake {
  border: 2px solid #d6936d;
}

.cream-input:checked + .cream {
  background-color: #aec6a5;
}

.coffee-input:checked + .coffee {
  background-color: #efd478;
}

.shake-input:checked + .shake {
  background-color: #d6936d;
}

.cream:before {
  content: "";
  width: 132px;
  height: 132px;
  background-image: linear-gradient(0deg, rgba(156, 190, 143, .7), rgba(156, 190, 143, .7)), linear-gradient(0deg, #d9d9d9, #d9d9d9);
  border-radius: 50%;
  position: absolute;
  top: 7px;
  box-shadow: 0 5px 20px rgba(10, 136, 23, .25);
}

.cream:after {
  content: "";
  width: 132px;
  height: 132px;
  background-image: url("icemodal.adbe27e5.png");
  position: absolute;
  top: 0;
}

.coffee:before {
  content: "";
  width: 132px;
  height: 132px;
  background-image: linear-gradient(0deg, rgba(248, 210, 78, .7), rgba(248, 210, 78, .7)), linear-gradient(0deg, #d9d9d9, #d9d9d9);
  border-radius: 50%;
  position: absolute;
  top: 7px;
  box-shadow: 0 5px 20px rgba(153, 114, 23, .25);
}

.coffee:after {
  content: "";
  width: 132px;
  height: 132px;
  background-image: url("coffeemodal.3fc76088.png");
  position: absolute;
  top: 0;
}

.shake:before {
  content: "";
  width: 132px;
  height: 132px;
  background-image: linear-gradient(0deg, rgba(213, 117, 63, .7), rgba(213, 117, 63, .7)), linear-gradient(0deg, #d9d9d9, #d9d9d9);
  border-radius: 50%;
  position: absolute;
  top: 7px;
  box-shadow: 0 5px 20px rgba(0, 0, 0, .25);
}

.shake:after {
  content: "";
  width: 132px;
  height: 132px;
  background-image: url("shakemodal.6c625ffe.png");
  position: absolute;
  top: 0;
}

